* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  position: sticky;
  top: 0;
  background: var(--bg);
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.navbar-logo {
  flex: 1;
  padding-left: 5rem;
}
.navbar-links {
  flex: 1;
  padding-right: 5rem;
}
.navbar-links-list {
  display: flex;
  justify-content: space-between;
}
.navbar-links-list a:hover {
  color: var(--secondary-colour);
}

html {
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 3.5rem;
}

a {
  text-decoration: none;
  font-size: 1.5rem;
  color: var(--primary-colour);
  font-weight: 700;
  transition: all 0.3s ease-in-out;
}

li {
  list-style: none;
}

:root {
  --bg: #fff;
  --primary-colour: #29b58a;
  --secondary-colour: #b52954;
}